<script>
import TableComponent from "@/components/Table/Table.vue";
import PaginationComponent from "@/components/Pagination1/Pagination1.vue";
export default {
  name: "functionalGroupSettingComponent",
  components: { TableComponent, PaginationComponent },
  data() {
    return {
      form: {
        userGroup: "",
        function: "",
      },
      rules: {
        userGroup: [{ required: false, message: "必填", trigger: "change" }],
        function: [{ required: false, message: "必填", trigger: "change" }],
      },
      tableColumns: [
        { id: "index", label: "項次", width: "70" },
        { id: "userGroup", label: "用戶群組", width: "" },
        { id: "menuName", label: "已有功能", width: "" },
      ],
      allFuns: [],
      mappings: [],
      groupFun: [],
      tableData: [],
      tableList: [],
    };
  },
  computed: {},
  methods: {
    async qryGroupFun() {
      const res = await this.$store.dispatch("qryGroupFun");
      const data = res.data.data;
      this.allFuns = data.allFuns;
      this.mappings = data.mappings;
      this.groupFun = data.groupFun;
    },
    async insGroupFun(data) {
      const res = await this.$store.dispatch("insGroupFun", data);
      if (res.data.isOK) {
        this.$message({
          type: "success",
          duration: "5000",
          showClose: true,
          message: "新增成功",
        });
        await this.qryGroupFun();
        this.search();
      }
    },
    changePage(page) {
      this.$store.state.currentPage = page;
      const cur = (page - 1) * 10;
      const last = page * 10;
      this.tableList = this.tableData.slice(cur, last);
    },
    // 取的欄位
    getMenuName(id) {
      const menuName = this.allFuns.find((e) => e.id === id).menuName;
      return menuName;
    },
    async delConfirmHandler(item) {
      const res = await this.$store.dispatch("delGroupFun", item);
      if (res.data.isOK) {
        this.$message({
          type: "success",
          duration: "5000",
          showClose: true,
          message: "刪除成功",
        });
        await this.qryGroupFun();
        this.search();
      }
    },
    search() {
      if (!this.form.userGroup) {
        this.rules.userGroup = [
          { required: true, message: "必填", trigger: "change" },
        ];
        this.rules.function = [];
        this.$refs.form.validateField("userGroup");
        this.$refs.form.clearValidate("function");
        this.$message({
          type: "error",
          duration: "5000",
          showClose: true,
          message: "請選擇用戶群組",
        });
        return;
      }
      this.$refs.form.clearValidate();
      this.$store.state.currentPage = 1;
      let data = JSON.parse(JSON.stringify(this.groupFun));
      this.tableData = data.filter(
        (e) => e.groupName === this.form.userGroup
      )[0].Detail;
      this.tableData.forEach((e, index) => (e.index = index + 1));
      this.tableList = this.tableData.slice(0, 10);
    },
    resetForm(formName) {
      this.rules = {};
      this.$refs.form.clearValidate();
      this.$refs[formName].resetFields();
    },
    submitForm(formName) {
      this.rules.userGroup = [
        { required: true, message: "必填", trigger: "change" },
      ];
      this.rules.function = [
        { required: true, message: "必填", trigger: "change" },
      ];
      this.$refs.form.validateField("userGroup");
      this.$refs[formName].validate(async (valid) => {
        if (valid) {
          const obj = {
            userGroup: this.form.userGroup,
            menuName: this.getMenuName(this.form.function),
            rowId: "",
            id: this.form.function,
            sortIndex: "",
          };
          this.insGroupFun(obj);
        } else {
          this.$message({
            type: "error",
            duration: "5000",
            showClose: true,
            message: "資料填寫錯誤",
          });
        }
      });
    },
  },
  mounted() {
    this.qryGroupFun();
    this.$store.commit("HEADER_HANDLER", true);
    this.$store.commit("BREADCRUMB_HANDLER", true);
    this.$root.$children[0].sidebarSwitch = true;
  },
};
</script>

<template src="./functionalGroupSetting.html"></template>
